*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

html{
  font-size: 62.5;
}

body{
  background-color: rgb(250, 235, 215);
  color:rgb(79, 59, 59);
  font-family: "Poppins",sans-serif;
}

.App{
  min-height: 100vh;
  display: flex;
}

.sideBar{
  flex:3;
  border-right: 1px solid rebeccapurple;
}

.main{
  flex:9;
}

.brand{
  margin-right: 1rem;
  font-size: 2rem;
}

.upperSide{
  padding:3rem;
  border-bottom: 1px solid rebeccapurple;
  height: 70%;
}

.upperSideTop{
  display: flex;
  align-items: center;
  margin-bottom:2.5rem;
}

.addBtn{
  height: 2rem;
  padding-right: 1rem;
}

.midBtn{
  background: rgb(90, 75, 255);
  border:none;
  color:white;
  padding: 1.5rem;
  font-size: 1.5rem;
  width:25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  margin-bottom: 3rem;
  border-radius: 0.5rem;
}

.query{
  display: flex;
  align-items: center;
  background: transparent;
  padding: 1.5rem;
  width:25rem;
  margin: 1rem auto;
  border-radius: 0.5rem;
  border:1px solid rgba(98,98,98,1);
  font-size: 1.5rem;
}

.query>img{
  margin-right: 2rem;
  object-fit: cover;
  height: 1.75rem;
}

.lowerSide{
  padding: 2rem;
}

.listItems{
  margin:1rem;
  display:flex;
  align-items: center;
  font-size: 1.3rem;
}

.listItemsImg{
  margin: 1rem;
  padding-right:0.75rem;
}

.listItems:nth-child(2)>.listItemsImg{
  margin-right:1.2rem;
}

.main{
  min-height: calc(100vh - 14rem);
  display:flex;
  flex-direction: column;
  align-items:center;
  margin: 6rem 10rem;
}

.chats{
  overflow: hidden;
  overflow-y:auto;
  scroll-behavior:smooth;
  width: 100%;
  max-width: 70rem;
  height: calc(100vh - 17rem);
}

.chat{
  margin: 1rem;
  padding: 2rem 3rem;
  font-size:1.2rem;
  display: flex;
  align-items: flex-start;
  text-align: justify;
}

.chatImg{
  object-fit: cover;
  width:3.5rem;
  margin-right: 2rem;
  border-radius: 0.5rem ;
}

.bot{
  background-color: rgb(243, 215, 178);
  width:fit-content;
  border-radius: 0.5rem;
}

.chatFooter{
  margin-top: auto;
  width:100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.inp{
  padding: 0.5rem;
  background: rgb(78, 61, 39);
  display: flex;
  align-items: center;
  border-radius: 0.5rem;
  width:66rem;
}

.inp>input{
  font-size: 1rem;
  width:calc(100% - 3rem);
  outline: none;
  padding: 1.25rem;
  color: white;
}

.inp>input, .send{
  background: transparent;
  border: none;
}